import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getAllGroups, groups } from "../../../redux/slices/GroupSlice";
import DataTable from "../../formElements/DataTable";
import { useNavigate } from "react-router-dom";
import { FaEdit } from "react-icons/fa";

import { FaLongArrowAltLeft } from "react-icons/fa";
import { tables, getAllTables } from "../../../redux/slices/AllTablesSlice";

const List = () => {
  const Tables = useSelector(tables);
  const Groups = useSelector(groups);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const result = [];
  useEffect(() => {
    if (Tables.length === 0) {
      dispatch(getAllTables());
    }
    if (Groups.length === 0) {
      dispatch(getAllGroups());
    }
  }, []);

  useMemo(() => {
    const tableGroupMap = new Map();
    Groups.forEach((grp) => {
      tableGroupMap.set(grp.id, grp.Groupname);
    });

    Tables.forEach((table) => {
      if (tableGroupMap.has(table.GroupId)) {
        const group = tableGroupMap.get(table.GroupId);
        result.push({ ...table, Groupname: group });
      }
    });
  }, [Tables, Groups]);
  const data = result.map((tg) => {
    return {
      id: tg.id,
      Table: tg.Tablename,
      Groupname: tg.Groupname,
      Capacity: tg.capacity,
      Status: tg.status,
      inactive: tg.inactive === 1 ? "Yes" : "No",
    };
  });
  const columns = useMemo(
    () => [
      {
        Header: "Table",
        accessor: "Table",
      },
      {
        Header: "Table Group",
        accessor: "Groupname",
      },
      {
        Header: "Capacity",
        accessor: "Capacity",
      },
      {
        Header: "Status",
        accessor: "Status",
      },
      {
        Header: "Inactive",
        accessor: "inactive",
      },
      {
        Header: "Edit",
        accessor: "id", // Assuming 'id' is the unique identifier for your rows
        Cell: ({ row }) => (
          <button
            onClick={() => {
              navigate("../", { state: { tableid: row.original.id } });
            }}
          >
            <FaEdit className="hover:text-orange-500" />
          </button>
        ),
      },
    ],
    []
  );

  return (
    <>
      <div className="px-7 py-1 overflow-auto flex flex-col   h-[calc(100vh-70px)]">
        <div className="inline-flex items-center">
          <span
            className=" inline-block p-2  text-slate-500 cursor-pointer hover:bg-slate-700 hover:text-slate-300 hover:rounded-lg "
            onClick={() => navigate("../")}
          >
            <FaLongArrowAltLeft></FaLongArrowAltLeft>
          </span>
          <span className=" text-slate-300 text-sm">Back</span>
        </div>
        {result.length > 0 ? (
          <DataTable columns={columns} data={data} />
        ) : (
          <p>No Tables Found</p>
        )}
      </div>
    </>
  );
};

export default List;
